/** CSS DONUT **/

:root {
    --chart-gray: #adadad;
    --chart-green: #288964;
    --chart-orange: #e66e19;
    --chart-red: #dc2d37;
    --chart-yellow: #dcaf00;
    --color-stroke-bar: var(--color-data-1);
    --donut-layer: #242424;
}

.light .donut {
    --color-data-1: #333;
    --color-data-2: #4a667e;
    --color-data-3: #23969a;
    --color-data-4: #976cf4;
    --color-data-5: #7f87ad;
    --donut-layer: #fafafa;
    --text-color: #242424;
}

.donut {
    text-align: center;
}

.donut .active-arc path {
    stroke: var(--donut-layer);
    stroke-width: 2px;
}

.donut .color-data-1 {
    fill: var(--color-data-1);
}

.donut .color-data-2 {
    fill: var(--color-data-2);
}

.donut .color-data-3 {
    fill: var(--color-data-3);
}

.donut .color-data-4 {
    fill: var(--color-data-4);
}

.donut .color-data-5 {
    fill: var(--color-data-5);
}

.donut .transparent {
    fill: rgba(0, 0, 0, 0);
}

.donut .value {
    font-size: 28px;

    fill: var(--text-color);
    text-anchor: middle;
}

.donut .unit {
    font-size: 16px;

    fill: var(--text-color);
    text-anchor: middle;
}

.donut .label {
    font-size: 16px;
    -webkit-transition: opacity .3s ease-in-out;
    -o-transition: opacity .3s ease-in-out;
    transition: opacity .3s ease-in-out;

    fill: var(--text-color);
    text-anchor: middle;
}

.donut path {
    -webkit-transition: fill .15s ease-in-out;
    -o-transition: fill .15s ease-in-out;
    transition: fill .15s ease-in-out;
}


